.cloud {
  position: relative;
  background: #ffffff;
  border: 3px solid #d4d4d5;
  border-radius: 10px;
}

.cloud p {
  font-size: 16px;
  line-height: 1.4;
  color: #6d6e71;
  margin: 7px 0;

  /*border: 1px solid red;*/
  box-sizing: border-box;
}

.cloud::after, .cloud::before {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.cloud::after {
  border-color: rgba(255, 255, 255, 0);
  border-top-color: #ffffff;
  border-width: 12px;
  margin-left: -12px;
}

.cloud::before {
  border-color: rgba(212, 212, 213, 0);
  border-top-color: #d4d4d5;
  border-width: 15px;
  margin-left: -15px;
}

